import React from 'react';
import {connect} from 'react-redux';
import {Redirect} from "react-router-dom";

import {withStyles} from '@material-ui/core/styles';
import {Grid, Typography} from '@material-ui/core';

import {signInFacebookRequest, signInGoogleRequest} from '../../AuthenticationApiActions';
import './SignIn.css';
import Login_Logo from '../../../../assets/svg/login_logo.svg';

import SocialSignUp from '../SocialSignUp/SocialSignUp';
import AppLoader from "../../../../utils/components/AppLoader/AppLoader";
import MessageModal from '../../../../utils/components/MessageModal/MessageModal';
import Footer from '../../../../utils/components/Footer/Footer';
// import Login_Logo from '../../../../assets/emailLogo.png';

const styles = theme => ({
  root: {
    backgroundColor: "#fff",
    height: '100%',
  },
  grid: {
    // paddingTop: 100,
  },
  quoteContainer: {
    [theme.breakpoints.down('md')]: {
      display: 'none'
    }
  },
  name: {
    marginTop: theme.spacing(3),
    color: theme.palette.white
  },
  bio: {
    color: theme.palette.white
  },
  content: {
    height: '90%',
    display: 'flex',
    flexDirection: 'column',
    backgroundColor: "#fff"
  },
  footerContainer: {
    position: 'fixed',
    bottom: 0,
    height: 50,
  },
  contentBody: {
    flexGrow: 1,
    display: 'flex',
    alignItems: 'center',
    [theme.breakpoints.down('md')]: {
      justifyContent: 'center'
    },
  },
  form: {
    flexBasis: 700,
    [theme.breakpoints.down('sm')]: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(2)
    }
  },
  title: {
    marginTop: theme.spacing(3),
    color: '#64748B'
  },
  socialButtons: {
    marginTop: theme.spacing(3)
  }
});

/**
 * Login page shown to the user when session is not present
 */
class SignIn extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      showLoader: false
    }
  }

  /**
   * Method executes when user sign in with google
   */
  signInWithGoogle = (data) => {
    this.setState({showLoader: true}, () => {
      this.props.signInWithGoogle(data).then(() => {
      }, (error) => {
        this.setState({
          showLoader: false
        }, () => {
          if (error.response && error.response.status === 403)
            this.showLoginError(error.response.data.error);
        });
      });
    });
  };

  /**
   * Method executes when user sign in with facebook
   */
  signInWithFacebook = (data) => {
    this.setState({showLoader: true}, () => {
      this.props.signInWithFacebook(data).then(() => {
      }, (errer) => {
        this.setState({
          showLoader: false
        }, () => {
          // if (error.response && error.response.status === 401)
          //   this.showLoginError(error.response.data.error);
        });
      });
    });
  };

  /**
   * Show modal with the message and title received from the server
   * @param loginErrorMessage
   * @param loginErrorTitle
   */
  showLoginError(loginErrorMessage = 'User name or password is incorrect.', loginErrorTitle = "Forbidden") {
    this.refs.messageModal.setState({
      showModal: true, message: loginErrorMessage, title: loginErrorTitle,
      showOkButton: true,
      handleOk: () => {
      },
      okText: 'Ok'
    });
  }

  /**
   * Render the UI of the login form
   * @returns {XML}
   */
  render() {
    document.title = 'CARE';
    const {classes} = this.props;

    if (this.props.loggedInUser) {
      return (<Redirect to="/"/>);
    } else {
      return (
        <>
          <div className={classes.root}>
            <div className={"sign-page-container"}>
              <Grid container className={'sign-page-grid-container'}>
                <Grid item lg={6} md={0} xs={0} className={[classes.quoteContainer, 'login-screen-image-container']}>
                  <img className='login-screen-application-logo-img' src={Login_Logo} alt="Loading..."/>
                </Grid>
                <Grid item lg={6} md={12} xs={12}>
                  <div className={classes.content}>
                    <div className={classes.contentBody}>
                      <form
                        className={classes.form}
                        onSubmit={this.handleSignIn}>

                        <div>
                          <Typography
                            align="center"
                            className={classes.title}
                            variant="h2">
                            Login <span className='login-separator'>|</span> Signup
                          </Typography>
                        </div>
                        <Typography
                          align="center"
                          color="textSecondary"
                          className="login-with-social-account"
                          gutterBottom
                        >
                          with your social account
                        </Typography>
                        <Grid
                          className={classes.socialButtons}
                          container
                          spacing={2}
                        >
                          <SocialSignUp
                            signInWithGoogle={(data) => this.signInWithGoogle(data)}
                            signInWithFacebook={(data) => this.signInWithFacebook(data)}
                          />
                        </Grid>
                      </form>
                    </div>
                  </div>
                </Grid>
              </Grid>
            </div>
            <Grid container className={classes.footerContainer}>
              <Grid
                item
                lg={12}
                xs={12}
              >
                <Footer/>
              </Grid>
            </Grid>
            <MessageModal
              ref="messageModal"
            />
            <AppLoader showLoader={this.state.showLoader}/>
          </div>
        </>
      );
    }
  }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    signUp: state.auth.signUp,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    signInWithGoogle: (data) => {
      return dispatch(signInGoogleRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    },
    signInWithFacebook: (data) => {
      return dispatch(signInFacebookRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });

    }
  }
};

SignIn = connect(
  mapStateToProps,
  mapDispatchToProps
)(SignIn);

export default withStyles(styles)(SignIn);
